.buttons {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;

    @media screen and (max-width: 350px) {}

    button {
        color: #000;
        background: #fff;
        padding: 0.65rem 1rem;
        border-radius: 10px;
        font-size: 0.85rem;
        text-transform: capitalize;
        cursor: pointer;
        border: none;
        margin-top: 1rem;
        font-family: "Poppins";


        &.active {
            background: #8c12f0;
            color: #fff;
        }
    }
}

.workImages {
    margin-top: 6.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4.2rem;

    @media screen and(max-width: 800px) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media screen and(max-width: 600px) {
        grid-template-columns: 1fr;
    }

    .workImage {
        max-width: 380px;
        height: 250px;
        margin-left: auto;
        margin-right: auto;
        position: relative;

        @media screen and(max-width:600px) {
            max-width: 350px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }


        .hoverLayer {
            background: yellow;
            width: 100%;
            height: 100%;
            z-index: 5;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            gap: 1.5rem;
            justify-content: center;
            align-items: center;

            h5 {
                display: flex;
                justify-content: flex-start;
            }

            a {
                width: 40px;
                height: 40px;
                background: rgba(255, 255, 255, 0.25);
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
                border: 2px solid black;

                svg {
                    color: black;
                    font-size: 1.5rem;
                }
            }
        }
    }
}